import * as React from 'react';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
// import IconButton from '@mui/material/IconButton';
// import NotificationsIcon from '@mui/icons-material/Notifications';
// import Badge from '@mui/material/Badge';
// import MenuIcon from '@mui/icons-material/Menu';
import Typography from '@mui/material/Typography';
import { Box, Input } from '@mui/material';
import { searchItem } from './bloc/items';


export default function Appbar() {
    return (
        <MuiAppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}>
            <Toolbar
                sx={{
                    pr: '24px', // keep right padding when drawer closed
                }}
            >
                <Typography
                    component="h1"
                    variant="h6"
                    color="inherit"
                    noWrap
                    sx={{ flexGrow: 1 }}
                >
                    ATÖLYE MTT Envanter sistemi
                </Typography>
                {/* <IconButton color="inherit">
                    <Badge badgeContent={4} color="secondary">
                        <NotificationsIcon />
                    </Badge>
                </IconButton> */}
                {/* searchbar */}
                <Box component="form" sx={{ display: 'flex', alignItems: 'center', backgroundColor: 'white', borderRadius: '5px', padding: '5px' }} noValidate autoComplete="off">
                <Input
                    placeholder="Search…"
                    inputProps={{ 'aria-label': 'search' }}
                    sx={{ width: '100%', maxWidth: 360, mr: 1 }}
                    onChange={(e) => {
                        searchItem(e.target.value);
                    }}
                />
                </Box>
            </Toolbar>
        </MuiAppBar>
    )
}